header:after,
#timeline .timeline-item:after,
header:before,
#timeline .timeline-item:before {
  content: '';
  display: block;
  width: 100%;
  clear: both;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

#timeline {
  margin: 30px auto;
  position: relative;
  padding: 0 10px;
  transition: all 0.4s ease;
}

#timeline:before {
  content: "";
  width: 3px;
  height: 100%;
  background: #090;
  left: 50%;
  top: 0;
  position: absolute;
}

#timeline:after {
  content: "";
  clear: both;
  display: table;
  width: 100%;
}

#timeline .timeline-item {
  padding-bottom: 50px;
  position: relative;
}

#timeline .timeline-item:last-child {
  padding-bottom: 0;
  position: relative;
}

#timeline .timeline-item .timeline-icon {
  background: #090;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0;
  left: 50%;
  overflow: hidden;
  margin-left: -23px;
  border-radius: 50%;
}

#timeline .timeline-item .timeline-icon p {
  position: relative;
  font-size: 25px;
  color: #fff;
  top: 5px;
  left: 18px;
}

#timeline .timeline-item .timeline-content {
  width: 45%;
  background: #fff;
  padding: 20px;
  box-shadow: 0 3px 0 rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  transition: all 0.3s ease;
}

#timeline .timeline-item .timeline-content p {
  padding-top: 20px;
}

#timeline .timeline-item .timeline-content h3 {
  padding: 15px;
  background: #090;
  color: #fff;
  margin: -20px -20px 0 -20px;
  font-weight: 300;
  border-radius: 3px 3px 0 0;
}

#timeline .timeline-item .timeline-content:before {
  content: '';
  position: absolute;
  left: 45%;
  top: 20px;
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 7px solid #090;
}

#timeline .timeline-item .timeline-content.right {
  float: right;
}

#timeline .timeline-item .timeline-content.right:before {
  content: '';
  right: 45%;
  left: inherit;
  border-left: 0;
  border-right: 7px solid #090;
}

/*** Table Styles **/

.table-title .table-fill {
  background: white;
  border-radius: 3px;
  border-collapse: collapse;
  height: 320px;
  margin: auto;
  max-width: 650px;
  padding: 5px;
  width: 100%;
  box-shadow: 0 5px 10px rgba(5, 94, 2, 0.1);
  animation: float 5s infinite;
}

.table-title th {
  color: #fff;
  background: #009900;
  border-bottom: 4px solid #9ea7af;
  border-right: 1px solid #34453b;
  font-size: 23px;
  font-weight: 300;
  padding: 10px;
  text-align: left;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  vertical-align: middle;
}

.table-title th:first-child {
  border-top-left-radius: 3px;
}

.table-title th:last-child {
  border-top-right-radius: 3px;
  border-right: none;
}

.table-title tr {
  border-top: 1px solid #C1C3D1;
  border-bottom-: 1px solid #C1C3D1;
  color: #666B85;
  font-size: 16px;
  font-weight: normal;
  text-shadow: 0 1px 1px rgba(255, 255, 255, 0.1);
}

.table-title tr:hover td {
  background: #25ad25;
  color: #FFFFFF;
  border-top: 1px solid #44834c;
}

.table-title tr:first-child {
  border-top: none;
}

.table-title tr:last-child {
  border-bottom: none;
}

.table-title tr:nth-child(odd) td {
  background: #EBEBEB;
}

.table-title tr:nth-child(odd):hover td {
  background: #25ad25;
}

.table-title tr:last-child td:first-child {
  border-bottom-left-radius: 3px;
}

.table-title tr:last-child td:last-child {
  border-bottom-right-radius: 3px;
}

.table-title td {
  background: #FFFFFF;
  padding: 10px;
  text-align: left;
  vertical-align: middle;
  font-weight: 300;
  font-size: 18px;
  text-shadow: -1px -1px 1px rgba(0, 0, 0, 0.1);
  border-right: 1px solid #C1C3D1;
}

.table-title td:last-child {
  border-right: 0px;
}

.table-title th.text-left {
  text-align: left;
}

.table-title th.text-center {
  text-align: center;
}

.table-title th.text-right {
  text-align: right;
}

.table-title td.text-left {
  text-align: left;
}

.table-title td.text-center {
  text-align: center;
}

.table-title td.text-right {
  text-align: right;
}

